body {
  /* background-color: #282c34; */
  background-color: #fbe9c7;

}

.line {
  fill: none;
  stroke: #4ea811;
  stroke-width: 3;
}

.axis-x line{
  stroke: none;

}

.axis-x path{
  stroke: none;
}


.axis-y text{
  text-decoration: none;
  text-emphasis: none;
  font-size: 200%;
  color: #5f5f5f;
}

.axis-x text{
  text-decoration: none;
  text-emphasis: none;
  font-size: 200%;
  color: #5f5f5f;
}

.overlay {
fill: none;
pointer-events: all;
}

/* Style the dots by assigning a fill and stroke */
.dot {
  fill: #ffab00;
  stroke: #fff;
}

.focus circle {
fill: none;
stroke: steelblue;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  /* min-height: 100vh; */
  /* display: flex; */
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-header h1 {
  font-size: calc(70px);
  margin-bottom:0px;
  color: #024c5b

}

h2, h3, h4, h5, h6{
  margin-top:0px;
  margin-bottom:10px;
}

.App-header h2 {
  font-size: calc(30px);
  color: #6B2E12;
}

.App-header h2 a {
  font-size: calc(30px);
  text-decoration: none;
  color: #6B2E12;
}
